							autocorrect="off" type="text" pattern="^[0-9]*[.,]?[0-9]*$" placeholder="0.0" minlength="1"
							maxlength="79" spellcheck="false" value="">
						<button @click="max_click()" class="hMWvPt">MAX</button>
						<button class="cqrovd">
							<span class="eYOSPp">
								<img src="../assets/ht.png" class="czHVaX">
								<span class="lprDva">HT</span>
								<svg width="12" height="7" viewBox="0 0 12 7" fill="none" class="sc-bFADNz clKNSx">
									<path d="M0.97168 1L6.20532 6L11.439 1" stroke="#AEAEAE"></path>
								</svg>
							</span>
						</button>
					</el-row>
