				<el-row type="flex" justify="center" style="margin-top:20px;">
					<svg t="1625653860510" class="icon" viewBox="0 0 1024 1024" version="1.1"
						xmlns="http://www.w3.org/2000/svg" p-id="3101" width="16" height="16">
						<path
							d="M901.581515 608.087997L538.403628 1007.67144c-0.402271 0.475412-0.329131 1.261669-0.731402 1.737081-0.457127 0.511982-0.255991 1.316524-0.767973 1.791936-0.493697 0.548552-1.279954 0.329131-1.828506 0.786258a36.021571 36.021571 0 0 1-22.326059 9.252241c-0.493697 0-0.877683 0.274276-1.389665 0.274276-0.182851 0-0.329131 0.091425-0.493697 0.091425-0.164566 0-0.310846-0.10971-0.457126-0.10971-0.822828 0.018285-1.426235-0.530267-2.249063-0.566837a33.717653 33.717653 0 0 1-18.321631-7.606586c-1.535945-1.133674-3.638727-0.676547-5.028392-2.102782-1.133674-1.097104-0.749688-2.815899-1.682226-4.040998-0.420556-0.530267-1.151959-0.841113-1.55423-1.426235L120.132852 608.124567A35.637584 35.637584 0 0 1 170.489911 557.730938l303.916003 334.305775V36.460412a36.460412 36.460412 0 1 1 72.920824 0v855.612871L851.206171 557.730938a35.601014 35.601014 0 1 1 50.375344 50.357059z"
							fill="#707070" p-id="3102">
						</path>
