							</span>
						</button>
					</el-row>
				</el-row>
				<el-row type="flex" justify="center" style="margin-top:20px;">
					<button @click="exchange_click()" class="fgcFZo">{{this.exchange_html}}</button>
				</el-row>
			</el-row>
