</template>

<script>
	import WalletConnect from "@walletconnect/client";
	import QRCodeModal from "@walletconnect/qrcode-modal";
	import Web3 from "web3";
	import {
		Loading
	} from 'element-ui';
	import Dictionaries from '../Dictionaries.js';
	const web3 = new Web3("https://http-mainnet-node.huobichain.com/");
	var M_connector = null
	export default {
		name: 'HelloWorld',
		data: function() {
			return {
				language:"en",
				lang:{},
				visible: false,
				from_amount: "-",
				to_amount: "-",
				from_value: "",
				to_value: "",
				exchange_html: "Connect Wallet",
				wallet_html: "Connect to a wallet",
				dialogTableVisible: false,
				connectAddress: "",
				Slippage:"",
				Toggle_switch:false,
				Disable_switch:false,
				Deadline:"",
				Connect_address:""
			}
		},
		created() {
			var temp = this
			this.lang = Dictionaries.Dictionaries[this.language]
			setInterval(function (){
				if (typeof window.ethereum !== 'undefined' && ethereum.selectedAddress != null && ethereum.selectedAddress.length == 42) {
					temp.Connect_address = ethereum.selectedAddress
				}
				if(temp.Connect_address.length ==42){
					temp.exchange_html = Dictionaries.Dictionaries[temp.language].Exchange
					temp.wallet_html = temp.Connect_address
					temp.Query(temp.Connect_address)
				}else{
					temp.exchange_html = Dictionaries.Dictionaries[temp.language].Connect_Wallet
					temp.wallet_html = Dictionaries.Dictionaries[temp.language].Connect_to_a_wallet
					temp.from_amount = "-"
					temp.to_amount = "-"
				}
			},1000);
			const connector = new WalletConnect({
				bridge: "https://bridge.walletconnect.org", // Required
				qrcodeModal: QRCodeModal,
			});
			M_connector = connector
			if (M_connector.connected) {
				if (M_connector._accounts != null && M_connector._accounts.length > 0) {
					this.Connect_address = M_connector._accounts[0]
					this.constructor_init(M_connector)
				}
			}
		},
		methods: {
			setUp() {
				this.$message({
					message: 'At the current pre-sale stage, price sliding point cannot be used'
				});
			},
			max_click() {
				if (this.from_amount != "-") {
					this.from_value = this.from_amount
					this.from_input()
				}
			},
			connectWalletConnect_click() {
				this.dialogTableVisible = false
				this.connectWallet()
			},
			connectMetamask_click() {
				var temp = this
				this.dialogTableVisible = false
				if (typeof window.ethereum !== 'undefined') {
					console.log('MetaMask is installed!');
					ethereum.request({
							method: "wallet_requestPermissions",
							params: [{
								eth_accounts: {}
							}]
						})
						.then((result) => {
							
						})
						.catch((error) => {
							temp.$message({
								message: 'User cancel authorization'
							});
						});
				} else {
					temp.$message({
						message: 'You need to install metamask'
					});
				}
			},
			wallet_click() {
				if (this.Connect_address.length == 42) {
					if (M_connector.connected) {
						M_connector.killSession()
					}
					this.Connect_address = ""
					ethereum.selectedAddress = ""
				} else {
					this.dialogTableVisible = true
				}
			},
			exchange_click() {
				const temp = this
				if (this.Connect_address == "") {
					this.dialogTableVisible = true
				} else if (this.Connect_address != "") {
					if (this.from_amount < this.from_value || this.from_value == 0) {
						this.$message({
							message: 'Your balance is not enough'
						});
						return
					}
					let loadingInstance = Loading.service({
						fullscreen: true,
						text: "Please wait a moment, the request is being sent"
					});
					const tx = {
						from: this.Connect_address, // Required
						to: "0xB7343c8aBCC0ed6A3Edd8157F5b03bf07201582A", // Required (for non contract deployments)
						value: web3.utils.toHex(web3.utils.toWei(this.from_value)),
						chainId: "0x01"
					};
					web3.eth.getGasPrice(function(err, wei) {
						tx.gasPrice = web3.utils.toHex(wei)
					})
					web3.eth.estimateGas(tx, function(err, gas) {
						if (err) {
							console.log(err)
							temp.$nextTick(() => { // 以服务的方式调用的 Loading 需要异步关闭
								loadingInstance.close();
							});
							temp.$message({
								message: 'Unable to pay for gas'
							});
							return
						}
						tx.gas = web3.utils.toHex(gas)
						if (M_connector.connected) {
							M_connector
								.sendTransaction(tx)
								.then((result) => {
									// Returns transaction id (hash)
									console.log(result);
									temp.$nextTick(() => { // 以服务的方式调用的 Loading 需要异步关闭
										loadingInstance.close();
									});
									temp.$message({
										message: 'Successful trade'
									});
								})
								.catch((error) => {
									temp.$nextTick(() => { // 以服务的方式调用的 Loading 需要异步关闭
										loadingInstance.close();
									});
									temp.$message({
										message: 'You turned down the deal'
									});
								});
						} else {
							ethereum.request({
									method: 'eth_sendTransaction',
									params: [tx],
								}).then(function(result) {
									console.log(result);
									temp.$nextTick(() => { // 以服务的方式调用的 Loading 需要异步关闭
										loadingInstance.close();
									});
									temp.$message({
										message: 'Successful trade'
									});
								})
								.catch(function(reason) {
									temp.$nextTick(() => { // 以服务的方式调用的 Loading 需要异步关闭
										loadingInstance.close();
									});
									temp.$message({
										message: 'You turned down the deal'
									});
								});
						}
					})
				}
			},
			connectWallet() {
				const connector = new WalletConnect({
					bridge: "https://bridge.walletconnect.org", // Required
					qrcodeModal: QRCodeModal,
				});
				if (!connector.connected) {
					// create new session
					connector.createSession();
				}
				M_connector = connector
				this.constructor_init(M_connector)
			},
			constructor_init(connector) {
				connector.on("connect", (error, payload) => {
					console.log("connect")
					if (error) {
						throw error;
					}
					const {
						accounts,
						chainId
					} = payload.params[0];
					this.Connect_address = accounts[0]
				});

				connector.on("session_update", (error, payload) => {
					console.log("session_update")
					if (error) {
						throw error;
					}
					const {
						accounts,
						chainId
					} = payload.params[0];
				});

				connector.on("disconnect", (error, payload) => {
					console.log("disconnect")
					if (error) {
						throw error;
					}
					this.Connect_address = ""
				});
			},
			Query(address) {
				const temp = this
				const abi = [{
					"constant": true,
					"inputs": [{
						"name": "_owner",
						"type": "address"
					}],
					"name": "balanceOf",
					"outputs": [{
						"name": "balance",
						"type": "uint256"
					}],
					"payable": false,
					"type": "function"
				}]
				const contract_address = "0x802Eb92dD5e5bcaB8970c8679FB2e2a3255C60f6"
				const contract = new web3.eth.Contract(abi, contract_address)
				contract.methods.balanceOf(address).call((err, result) => {
					console.log(result)
					temp.to_amount = web3.utils.fromWei(result);
				})
				web3.eth.getBalance(address, function(err, res) {
					temp.from_amount = web3.utils.fromWei(res);
				})
			},
			from_input() {
				this.to_value = this.from_value * 2000;
			},
			to_input() {
				this.from_value = this.to_value / 2000;
			}
		}
	}
</script>

